@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
*{
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
}

.element-container {
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    justify-content: space-between;
}

.container1 {
    display: flex;
}
* {
    margin: 0;
    padding: 0;
}

.element-name {
    height: 42vh;
    width: 29vw;
    background-color: #f3e345;
    font-size: 80px;
    /* font-weight: bold; */
}

.atomic-n {
    height: 14vh;
    margin-left: 30px;
    display: flex;
    align-items: center;
}

.element-atom {
    height: 14vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.atomic-mass {
    height: 14vh;
    float: right;
    margin-right: 30px;
    display: flex;
    align-items: center;
}

* {
    margin: 0;
    padding: 0;
}

.Element-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 42vh;
    width: 68vw;
    background-color: #f3e345;
    margin-left: 40px;
    font-size: 30px;
}

.container2 {
    margin-top: 4vh;
    margin-left: 40px;
}

.ec-container {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.ec-value {
    margin-left: 40px;
    font-size: 35px;    
}

.vse-container {
    display: flex;
    margin-bottom: 30px;
    align-items: center;
}

.vse {
    margin-left: 10px;
}
.vse-value {
    margin-left: 50px;
}

.melting-point-container {
    display: flex;
    margin-bottom: 20px;
    align-items: center;
}

.melting-point {
    margin-left: 40px;
}

.melting-point-value {
    margin-left: 85px;
    border: 2px solid black;
    padding: 5px 15px;
}

.boiling-point-container {
    display: flex;
    align-items: center;
}

.boiling-point {
    margin-left: 40px;
}

.boiling-point-value {
    margin-left: 95px;
    border: 2px solid black;
    padding: 7px 17px;
}

* {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    color: black;
}

.element {
    height: 25vh;
    width: 14vw;
    background-color: #01edf9;
    margin-top: 10vh;
    /* margin-left: 20px; */
}


.element:hover {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
}


.atomic-number {
    height: 7vh;
    margin-left: 25px;
    font-size: 5vh;
}


.symbol {
    display: flex;
    justify-content: center;
    font-weight: bold;
    font-size: 8vh;
    height: 18vh;
}
